<template>
  <div class="home">
    <!-- 搜索框 -->
    <div>
      <van-search v-model="value" placeholder="请输入搜索关键词" @click="btn" />
    </div>
    <div class="container-box" @scroll="handleScroll($event)">
      <!-- 热门轮播图 -->
      <div class="Banner-box">
        <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
          <van-swipe-item v-for="(bannerItem, bannerIndex) in BannerData" :key="bannerIndex">
            <img :src="bannerItem.img" alt />
          </van-swipe-item>
        </van-swipe>
      </div>
      <!-- 导航列表 -->
      <div class="select-box">
        <div class="ranking-box" @click="goRanKing">
          <img src="../assets/排行.png" alt />
          <p>排行</p>
        </div>
        <div class="free-box" @click="goFree">
          <img src="../assets/免费.png" alt />
          <p>免费</p>
        </div>
      </div>
      <!-- 推荐模块 -->
      <div class="recom-box">
        <div
          class="img-item"
          v-for="(recomItem,recomIndex) in recomData"
          :key="recomIndex"
          @click="goIndetail(recomItem.item_id)"
        >
          <img v-lazy="recomItem.image" />
          <div class="title-box">
            <div class="title">{{recomItem.title}}</div>
            <div
              v-if="recomItem.comic_info.decision !== '' "
              class="decision"
            >{{recomItem.comic_info.decision}}</div>
            <div v-else>
              <div class="type">{{recomItem.comic_info.main_style_name}}</div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Home",
  data() {
    return {
      value: null,
      isShowSearch: true,
      BannerData: [],
      recomData: [],

      // 分页
      pagNum: 20
    };
  },
  created() {
    this.getBanner();
    this.getrecom();
  },
  methods: {
    // 页面滚动事件
    handleScroll(e) {
      const { scrollTop, clientHeight, scrollHeight } = e.target;
      // 
      if (scrollTop + clientHeight === scrollHeight) {
        
        (this.pagNum += 20), this.getrecom();
      }
    },

    // 搜索框被点击
    btn() {
      this.$router.push({
        name: "Hotsearch"
      });
    },
    // 前往免费
    goFree() {
      this.$router.push({ name: "Free" });
    },
    // 前往漫画排行榜
    goRanKing() {
      this.$router.push({
        name: "Ranking"
      });
    },
    // 前往漫画详细
    goIndetail(id) {
      this.$router.push({
        name: "Indetail",
        params: {
          id
        }
      });
    },

    getBanner() {
      this.axios({
        method: "get",
        url: "/Banner"
      })
        .then(res => {
          //  
          this.BannerData = res.data.data;
          //  
        })
        .catch(err => {
          
        });
    },
    getrecom() {
      this.axios({
        method: "get",
        url: "/HomeFeed",
        params: {
          pageNum: 1,
          pageSize: this.pagNum
        }
      })
        .then(res => {
          this.recomData = res.data.data.feeds;
        })
        .catch(err => {
          
        });
    }
  },
  computed: {
    mybook() {
      return this.$store.state.book;
    }
  }
};
</script>

<style lang="scss" scoped>
.home {
  .my-swipe .van-swipe-item {
    color: #fff;
    font-size: 20px;
    line-height: 150px;
    text-align: center;
    background-color: #39a9ed;
  }
  .van-search__content {
    border-radius: 25px;
  }
  .onClassify {
    border-radius: 25px;
    padding: 0px 20px;
  }
  .container-box {
    position: fixed;
    left: 0;
    right: 0;
    top: 54px;
    bottom: 50px;
    margin: auto;
    overflow-y: auto;
    .Banner-box {
      width: 100%;
      img {
        display: block;
        width: 100%;
      }
    }
    .my-swipe .van-swipe-item {
      color: #fff;
      font-size: 20px;
      line-height: 150px;
      text-align: center;
      background-color: #39a9ed;
    }
    .recom-box {
      width: 100%;
      display: flex;
      flex-wrap: wrap;
      margin-top: 20px;
      padding: 0px 10px;
      box-sizing: border-box;
      .img-item {
        width: calc(100% / 2 - 5px);
        margin-right: 10px;
        margin-bottom: 15px;
        border-radius: 5px;
        box-sizing: border-box;
        text-align: center;
        overflow: hidden;

        &:nth-child(2n) {
          margin-right: 0px;
        }
        img {
          width: 100%;
        }
        .title-box {
          font-weight: bold;
          font-size: 15px;
          text-align: left;
          //  padding: 5px 10px;
          margin-top: 5px;
          .title {
            width: 100%;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
          }
          .decision {
            display: inline-block;
            background-color: #fef3e1;
            color: #ef9e29;
            font-size: 13px;
            font-weight: 300;
            padding: 0 5px;
            border-radius: 5px;
            margin: 4px 0;
          }
          .type {
            margin: 5px 0;
            font-weight: 300;
            font-size: 13px;
            color: #ccc;
            margin: 4px 0;
          }
        }
      }
    }
    .select-box {
      margin: 10px 0px;
      width: 100%;
      height: 50px;
      padding: 0px 10px;
      box-sizing: border-box;
      display: flex;
      justify-content: center;
      align-items: center;
      & > div {
        width: 30px;
        text-align: center;
        padding: 0px 50px;
        img {
          width: 100%;
          height: 30px;
        }
      }
    }
  }
  ::v-deep .van-swipe__track {
    height: 100%;
  }
}
</style>